@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
#intro_actividad04 {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  overflow: hidden;
  background-image: url("/build/img/contenido/Pag04/fondo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#intro_actividad04::after {
  content: "";
  background: #3d3d3d;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40%;
  z-index: -1;
}
@media (max-width: 768px) {
  #intro_actividad04 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
  }
}
#intro_actividad04 .cont-vineta {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
#intro_actividad04 .cont-vineta #vineta04 {
  margin-top: 8rem;
  border: 5px solid #3b5895;
  background: #ffffff;
  width: 90%;
  padding: 2rem;
  border-radius: 2rem;
  line-height: 1.6;
  position: relative;
}
@media (max-width: 768px) {
  #intro_actividad04 .cont-vineta #vineta04 {
    margin-top: 1rem;
  }
}
#intro_actividad04 .cont-vineta #vineta04::before {
  content: "";
  position: absolute;
  top: 20%;
  left: calc(100% + 0.4rem);
  width: 3rem;
  height: 4rem;
  background: #3b5895;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 768px) {
  #intro_actividad04 .cont-vineta #vineta04::before {
    top: calc(100% + 0.4rem);
    left: 40%;
    width: 4rem;
    height: 3rem;
    clip-path: polygon(0 0, 100% 0%, 50% 100%);
  }
}
#intro_actividad04 .cont-vineta #vineta04 #frase01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad04 .cont-vineta #vineta04 #frase02 {
  color: #129a63;
  font-weight: 700;
}
#intro_actividad04 .cont-imagen {
  min-height: 100%;
}

