#cont-respuestas {
  margin-top: 2rem;
  background-image: url("/build/img/contenido/Pag04/fondo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#cont-respuestas .fondo {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
#cont-respuestas .respuestas {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
@media (max-width: 768px) {
  #cont-respuestas .respuestas {
    flex-direction: column-reverse;
  }
}
#cont-respuestas .respuestas .inciso {
  width: 10%;
  font-size: 4.2rem;
  padding: 2rem;
  color: #ffffff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 768px) {
  #cont-respuestas .respuestas .inciso {
    width: 100%;
    justify-content: center;
  }
}
#cont-respuestas .respuestas .respuesta {
  width: 85%;
  text-align: center;
  clip-path: polygon(5% 0, 100% 0%, 100% 100%, 0% 100%);
  background: #ffffff;
  padding: 2rem 4rem;
}
@media (max-width: 768px) {
  #cont-respuestas .respuestas .respuesta {
    width: 100%;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%);
  }
}
#cont-respuestas .respuestas#resp01 {
  background: #129a63;
  border-radius: 2rem;
  border: 5px solid #108857;
  color: #129a63;
}
#cont-respuestas .respuestas#resp02 {
  background: #3b5895;
